___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

header {
  text-align: center;
  background-color: #ccc;
  padding: 2rem 0.8rem 1.8rem;
}

body {
  min-height: 80vh;
  font-size: 1rem;
  background-color: #f5f5f5;
  min-width: 70%;
  max-width: 100%;
}

section.score-container {
  display: flex;
  gap: 4rem;
  justify-content: center;
  width: 100%;
  overflow: hidden;
}

.recent-score .recent-score-header {
  display: flex;
  flex-direction: row;
  gap: 3rem;
}

.recent-score {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.score-list {
  border: 3px solid #ccc;
}

h2 {
  margin: 0;
  text-align: center;
  margin-top: 2rem;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 2rem;
}

input {
  padding: 0.25rem;
  border: none;
  border-radius: 0.25rem;
  box-shadow: inset 0 0 0 1px #ccc;
}

button {
  background-color: #fff;
  color: #000;
  padding: 0.3rem 0.6rem;
  border-radius: 0.25rem;
  cursor: pointer;
  align-self: flex-end;
  box-shadow: 4px 4px 0 #000;
  border: 1px solid #000;
}

.recent-score ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.recent-score ul li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}

.score-list li:nth-child(odd) {
  background-color: #f4f2f2;
}

.error {
  background-color: #eee;
  color: #ff0707;
  border-radius: 0.5rem;
  font-size: 1rem;
  padding: 0.8rem;
  font-weight: 600;
  min-width: 40%;
  max-width: 100%;
  text-align: center;
}

.success {
  background-color: #eee;
  color: rgb(10, 193, 10);
  border-radius: 0.5rem;
  font-size: 1rem;
  padding: 0.8rem;
  font-weight: 600;
  min-width: 40%;
  max-width: 100%;
  text-align: center;
}
